"use client";

import React, { useState, useEffect } from 'react';
import { Search, Filter, BookOpen, Clock, Users, Star, ChevronRight } from 'lucide-react';
import Image from 'next/image';
import Link from 'next/link';
import { useSession } from 'next-auth/react';

interface Course {
    _id: string;
    code: string;
    name: string;
    description: string;
    credits: number;
    level: string;
    difficulty: 'beginner' | 'intermediate' | 'advanced';
    delivery: string[];
    language: string;
    school: {
        name: string;
        shortName: string;
    };
    programs: Array<{
        program: {
            name: string;
            code: string;
        };
        semester: number;
    }>;
    professors: Array<{
        name: string;
        rating?: number;
    }>;
    stats: {
        enrollmentCount: number;
        rating: {
            average: number;
            count: number;
        };
        materialCount: number;
    };
}

const CoursesPage = () => {
    const { data: session } = useSession();
    const [courses, setCourses] = useState<Course[]>([]);
    const [loading, setLoading] = useState(true);
    const [searchTerm, setSearchTerm] = useState('');
    const [filters, setFilters] = useState({
        level: '',
        difficulty: '',
        delivery: '',
        school: '',
        course: '',
        sort: 'relevance'
    });
    const [currentPage, setCurrentPage] = useState(1);
    const [totalPages, setTotalPages] = useState(1);

    // Mock data for development
    const mockCourses: Course[] = [
        {
            _id: '1',
            code: 'CS101',
            name: 'Introduction to Computer Science',
            description: 'Fundamental concepts of computer science including programming basics, algorithms, and data structures.',
            credits: 3,
            level: '1',
            difficulty: 'beginner',
            delivery: ['in-person', 'online'],
            language: 'english',
            school: {
                name: 'University of Toronto',
                shortName: 'UofT'
            },
            programs: [{
                program: {
                    name: 'Computer Science',
                    code: 'CS'
                },
                semester: 1
            }],
            professors: [{
                name: 'Dr. Sarah Johnson',
                rating: 4.5
            }],
            stats: {
                enrollmentCount: 245,
                rating: {
                    average: 4.3,
                    count: 89
                },
                materialCount: 12
            }
        },
        {
            _id: '2',
            code: 'MATH200',
            name: 'Calculus II',
            description: 'Integral calculus, applications of integration, infinite series, and differential equations.',
            credits: 4,
            level: '2',
            difficulty: 'intermediate',
            delivery: ['in-person'],
            language: 'english',
            school: {
                name: 'York University',
                shortName: 'YorkU'
            },
            programs: [{
                program: {
                    name: 'Mathematics',
                    code: 'MATH'
                },
                semester: 3
            }],
            professors: [{
                name: 'Prof. Michael Chen',
                rating: 4.8
            }],
            stats: {
                enrollmentCount: 189,
                rating: {
                    average: 4.6,
                    count: 67
                },
                materialCount: 15
            }
        },
        {
            _id: '3',
            code: 'BUS301',
            name: 'Digital Marketing Strategy',
            description: 'Comprehensive study of digital marketing channels, analytics, and campaign optimization.',
            credits: 3,
            level: '3',
            difficulty: 'intermediate',
            delivery: ['online', 'hybrid'],
            language: 'english',
            school: {
                name: 'Ryerson University',
                shortName: 'TMU'
            },
            programs: [{
                program: {
                    name: 'Business Administration',
                    code: 'BUS'
                },
                semester: 5
            }],
            professors: [{
                name: 'Dr. Emily Rodriguez',
                rating: 4.2
            }],
            stats: {
                enrollmentCount: 156,
                rating: {
                    average: 4.1,
                    count: 43
                },
                materialCount: 8
            }
        }
    ];

    useEffect(() => {
        // Simulate API call
        const fetchCourses = async () => {
            setLoading(true);
            // In real app, this would be an API call
            setTimeout(() => {
                setCourses(mockCourses);
                setLoading(false);
            }, 1000);
        };

        fetchCourses();
    }, [searchTerm, filters, currentPage]);

    const getDifficultyColor = (difficulty: string) => {
        switch (difficulty) {
            case 'beginner': return 'bg-green-100 text-green-800';
            case 'intermediate': return 'bg-yellow-100 text-yellow-800';
            case 'advanced': return 'bg-red-100 text-red-800';
            default: return 'bg-gray-100 text-gray-800';
        }
    };

    const filteredCourses = courses.filter(course => {
        const matchesSearch = course.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                            course.code.toLowerCase().includes(searchTerm.toLowerCase()) ||
                            course.description.toLowerCase().includes(searchTerm.toLowerCase());
        
        const matchesLevel = !filters.level || course.level === filters.level;
        const matchesDifficulty = !filters.difficulty || course.difficulty === filters.difficulty;
        const matchesDelivery = !filters.delivery || course.delivery.includes(filters.delivery);
        const matchesSchool = !filters.school || course.school.shortName === filters.school;

        return matchesSearch && matchesLevel && matchesDifficulty && matchesDelivery && matchesSchool;
    });

    const programFilters = [
        "All Programs", "Engineering", "Business", "Arts & Sciences", "Health Sciences"
    ];
    const courseFilters = [
        "All Courses", "Calculus I", "Linear Algebra", "Organic Chemistry", "Introduction to Psychology"
    ];
    const sortOptions = [
        { label: "Relevance", value: "relevance" },
        { label: "Popularity", value: "popularity" },
        { label: "Date", value: "date" }
    ];

    if (loading) {
        return (
            <div className="min-h-screen bg-gray-50 p-6">
                <div className="max-w-7xl mx-auto">
                    <div className="animate-pulse">
                        <div className="h-8 bg-gray-300 rounded w-1/4 mb-6"></div>
                        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                            {[1, 2, 3, 4, 5, 6].map(i => (
                                <div key={i} className="bg-white rounded-lg shadow-md p-6">
                                    <div className="h-4 bg-gray-300 rounded w-3/4 mb-2"></div>
                                    <div className="h-4 bg-gray-300 rounded w-1/2 mb-4"></div>
                                    <div className="h-20 bg-gray-300 rounded mb-4"></div>
                                    <div className="flex justify-between">
                                        <div className="h-4 bg-gray-300 rounded w-1/4"></div>
                                        <div className="h-4 bg-gray-300 rounded w-1/4"></div>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-[#1a1a1a] text-white">
            {/* Header */}
            <div className="w-full border-b border-[#232425]">
                <div className="max-w-6xl mx-auto px-6 py-10">
                    <h1 className="text-4xl font-bold mb-2">Community Materials</h1>
                    <p className="text-gray-300 mb-8 max-w-2xl">
                        Explore a wealth of resources shared by your peers, categorized by program and course. Find exactly what you need to excel in your studies.
                    </p>
                    {/* Program Filters */}
                    <div className="flex flex-wrap gap-3 mb-4">
                        {programFilters.map((filter) => (
                            <button
                                key={filter}
                                className={`px-5 py-2 rounded-lg font-medium transition-colors
                                    ${filters.school === filter || (filter === "All Programs" && !filters.school)
                                        ? "bg-[#232425] text-white"
                                        : "bg-[#36454F]/60 text-gray-300 hover:bg-orange-300 hover:text-white transition-colors"}
                                `}
                                onClick={() => setFilters(f => ({ ...f, school: filter === "All Programs" ? "" : filter }))}
                            >
                                {filter}
                            </button>
                        ))}
                    </div>
                    {/* Course Filters */}
                    <div className="flex flex-wrap gap-3 mb-8">
                        {courseFilters.map((filter) => (
                            <button
                                key={filter}
                                className={`px-4 py-2 rounded-lg font-medium transition-colors
                                    ${filters.course === filter || (filter === "All Courses" && !filters.course)
                                        ? "bg-[#232425] text-white"
                                        : "bg-[#36454F]/60 text-gray-300 hover:bg-orange-300 hover:text-white transition-colors"}
                                `}
                                onClick={() => setFilters(f => ({ ...f, course: filter === "All Courses" ? "" : filter }))}
                            >
                                {filter}
                            </button>
                        ))}
                    </div>
                    {/* Sort By */}
                    <div className="mb-2">
                        <span className="font-semibold mr-4">Sort By</span>
                        {sortOptions.map(opt => (
                            <button
                                key={opt.value}
                                className={`px-4 py-2 rounded-lg font-medium transition-colors mr-2
                                    ${filters.sort === opt.value || (!filters.sort && opt.value === "relevance")
                                        ? "bg-[#36454F] text-white"
                                        : "bg-[#36454F]/60 text-gray-300 hover:bg-[#232425]"}
                                `}
                                onClick={() => setFilters(f => ({ ...f, sort: opt.value }))}
                            >
                                {opt.label}
                            </button>
                        ))}
                    </div>
                </div>
            </div>

            {/* Course List */}
            <div className="max-w-6xl mx-auto px-6 py-10">
                <div className="flex flex-col gap-8">
                    {filteredCourses.map((course, idx) => (
                        <div
                            key={course._id}
                            className="flex flex-col md:flex-row items-stretch bg-[#232425] rounded-xl overflow-hidden shadow-lg"
                        >
                            {/* Info */}
                            <div className="flex-1 p-6 flex flex-col justify-center">
                                <span className="text-sm text-gray-400 mb-1">{course.school.name}</span>
                                <h2 className="text-xl font-bold mb-1">{course.name}</h2>
                                <p className="text-gray-200 font-medium mb-2">{course.code}</p>
                                <p className="text-gray-300 mb-4">{course.description}</p>
                                <div className="flex flex-wrap gap-4 text-sm text-gray-400">
                                    <span>{course.credits} credits</span>
                                    <span>Level {course.level}</span>
                                    <span className="capitalize">{course.difficulty}</span>
                                    <span>{course.stats.enrollmentCount} enrolled</span>
                                    <span>
                                        <Star className="inline w-4 h-4 text-yellow-400 mr-1" />
                                        {course.stats.rating.average} ({course.stats.rating.count})
                                    </span>
                                </div>
                            </div>
                            {/* Image */}
                            <div className="md:w-64 flex-shrink-0 flex items-center justify-center bg-[#232425]">
                                <Image
                                    src={`https://source.unsplash.com/400x300/?study,notes,${idx}`}
                                    alt={course.name}
                                    width={256}
                                    height={192}
                                    className="object-cover w-full h-full"
                                />
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
};

export default CoursesPage; 